.listings-container {
    margin-top: 3%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-direction: column;
  }
  
  .listings-title {
    margin-top: 3%;
    text-align: center;
    color: black;
  }
  
  .canva-embed {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: 56.2500%;
    padding-bottom: 0;
    margin-top: 1.6em;
  }
  
  .canva-iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border: none;
    padding: 0;
    margin: 0;
  }